body{
    margin: 0;
    padding: 0;
    width: 100%;
    
}
.hide{
    display: none;
}
.contain-30{
    width: 100%;
    padding: 0 30px;
}
.contain{
    width: 100%;
    padding: 0 60px;

}

/* font-family: 'Roboto', sans-serif; */

/* ***************HOME PAGE CSS START***************** */

.OlxHeading{
    letter-spacing: 0.05em;
    display: inline-block;
    margin-top: 13px;
    margin-left:5px;
}
.select{
    background-color: white;
    width: 260px;
    height: 42px;
    font-size: 20px;
  
}

.nav,.row{
    background-color: whitesmoke!important;
}
ul{
    list-style-type: none;

}
@media(max-width:400px){
    ul,li{
        display: none;
    }
    

}


.searchBar{
width: 90%;
margin: 10px 0px 10px 5px; 
padding: 7px 10px;
font-size:18px;
color:rgb(141, 141, 141);
border:2px solid #002f34;
border-radius: 3px;

}
.fa-search{
    color:#ffff;
}
.input-group .btn{
    transform: translateY(23%);
    height: 81%;
}
.searchBar:focus{
    border-color: aquamarine!important;

}
.navBtnsContainer{
    margin-top: 7px;
}
.loginBtn{
    background: whitesmoke;
    border:none;
    outline: none;
    font-size: normal;
    border-bottom: 2px solid black;
    font-weight: bold;
    padding: 0;
    font-family: Roboto,Arial,Helvetica,sans-serif;
    
}
.loginBtn:hover{
    border:none

}

.sellBtn{

    font-family: Roboto,Arial,Helvetica,sans-serif;
    margin: 5px 20px ;
    margin-left: 30px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: bolder;
    padding :7px 15px 7px 15px;
    background-color: white;
    border: 5px solid red;
    border-left-color: yellow;
    border-top-color: blue;
    border-bottom-color: aqua;
    border-radius: 25px;
    outline: none;
    
}
.sellBtn i{
    margin: 0px 5px;
}

/* *************** HOME PAGE Links==> CSS START ***************** */
.listContainer{

    box-shadow: 0px 0px 1px rgb(104, 104, 104);
    margin: 0;
}
.list{
    list-style-type: none;
    display: flex;
    margin: 0;
}

.list li{
    padding:10px 10px;
}
.list li:hover{
    text-decoration: none;
    color: teal;
    
}



.frontImageDiv{
    padding:0;
    overflow:hidden ;
margin: 0;
}
.frontImageDiv img{
    margin: 0;
    width: 99%;
    height: 260px;
    transform: scaleX(1.7111);

}


.cardsSection{
    padding: 60px 0px;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center;
}
.row{
    background-color: #fff;
}

.freshHeading{
    font-family: 'Roboto', sans-serif;
    padding: 5px 10px;
}

.card{
    margin: 5px;
    position: relative;
    min-width: 18rem;
    max-width: 20rem;
    height: 17rem;
    box-shadow: 0px 0px 1px rgba(0,0,0,0.5);
    display: flex;
    align-items:stretch;
    justify-content: center;
}
.card h6{
    font-size: 20px;
    letter-spacing: 0.05em;
    text-align: center;
    padding: 3px;
    

}
.card img{
    width:10rem;
    height:7rem;
    align-self: center;
    margin:5px;

}
.card .price{
    align-self:stretch;
    font-size: 20px;
    font-weight: bolder;
    font-family: 'Roboto', sans-serif;
    border-top: 0.5px solid rgba(0, 0, 0, 0.2);
    
    padding: 5px 10px;
    
}
.cardBtn{
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    padding: 5px 10px;
    color: rgba(250, 250, 3, 0.808);
    background-color: rgba(0, 0, 0, 0.6);
    border: none;
    outline: none;
    border-radius: 3px;
    box-shadow: 0px 0px 1px rgba(24, 24, 24,0.6);
}
.statusSpan{
    position: absolute;
    top: 0;
    left: 5px;
    background-color: yellow;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: bold;
    font-family: 'Roboto-Regular', sans-serif;
    color: rgba(0, 0, 0, 0.834);
    padding: 3px 7px;
    border-radius: 3px;
    

}
.description{
    clear:both;
    font-weight:normal!important;
    font-size: 14px!important;
    margin-bottom:1px;

}
.date{
    float: right;
    font-weight: normal;
    font-size: 14px;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.5);
}
.locationName{
    float: left;
    font-weight: normal;
    font-size: 14px;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.5);
}
.Line{
    border-left:5px solid yellow;
}
.beside{
    
    padding-left:10px;
    
}


/* *************** HOME PAGE CSS END ***************** */

/*  **************addMoreBtn CSS START******************* */
.addBtnSection{
padding-bottom:40px;    
}
.Flex{

    display: flex;
    justify-content: center;

}


.addMoreBtn{
    color:#212121;
    padding: 10px;
    border: 2px solid #212121;
    border-radius: 3px;
    font-family: 'Roboto-Regular', sans-serif;
    font-weight: 600;
    margin: 0;
    background-color: white;
}

.addMoreBtn:hover{
    border-inline-start: 5px solid #212121;
    border-inline-end: 5px solid #212121;
}





