.belowLoadSection{
    padding: 0px 20px;
background-color:#F5F5F5;
display: flex;
flex-wrap: wrap;
justify-content: center;
align-items: center;
}
.imageDiv{
    flex-grow: 3;
}
.imageDiv img{
    min-width: 300px;
    max-width: 400px;
    margin-left:40px ;
}
.tryTheOlxApp{
    flex-grow: 2;
    padding: 0px 30px;
    min-width: 450px;
    max-width: 400px;
    height: 150px;
    border-right: 2px solid rgba(42, 42, 42,0.2);
}
.belowLoadSection .tryTheOlxApp h2{
    font-family: 'Roboto-Regular', sans-serif;
    color: #37474F;
    font-weight: 700;
}
.tryTheOlxApp p{
    font-size: 20px;
    font-weight: 400;
    color: #37474F;
    font-family: 'Roboto-Regular', sans-serif;
}
.GoogleAppLinks{
flex-grow: 2;
}
.GoogleAppLinks .GoogleAppLinksContainer{
padding:0px 30px;
}
.links{
display: flex;
justify-content: space-around;

}
/* .GoogleAppLinks span{
    display: inline-block;
} */

.GoogleAppLinks img{
    width: 140px;
    height: auto;    
}
.GoogleAppLinks h6{
    font-family: 'Roboto-Regular', sans-serif;
    font-weight: bolder;
    text-transform: uppercase;
}

@media(max-width:1269px){

    .GoogleAppLinks{
        padding: 10px 0px;
    }
    .links{
        justify-content: center;
    }
    .GoogleAppLinks img{
        margin: 0px 5px;
    }
}
@media(max-width:1024px){


}

@media(max-width:929px){

    .tryTheOlxApp{
        padding: 10px 0px;
        flex-grow: 1;
    }
    .imageDiv{
        flex-grow: 1;
    }

}

@media(max-width:843px){
    .tryTheOlxApp{
        display: none;
    }

}
@media(max-width:820px){
    .GoogleAppLinks{
        padding: 10px 0px;
    }

}

@media(max-width:440px){
    .GoogleAppLinksContainer{
        padding:10px;
    }
    .imageDiv img{
        width: 250px;
        margin-left:20px ;
    }

}

